import React,{Component} from "react";
import {Row,Col} from "antd";
import "../css/cvrezyume.css"
import myfoto from "../images/myfoto.jpg"
import myresume from "../cvrezyume/my_resume.pdf";
import Aos from "aos";
import 'aos/dist/aos.css';
import {connect} from "react-redux";
import {UzLanguage} from "../Redux/Actions/UzLanguage";
import {RuLanguage} from "../Redux/Actions/RuLanguage";
Aos.init({
    duration:1500
})
class Cvrezyume extends Component{
    render() {
        const {uzLang}= this.props;
        return (
            <div>
                <section className=" section " id="resume">
                    <h2 data-aos={"zoom-in"} style={{textAlign:"center",color:"black",paddingTop:"50px",paddingBottom:"50px"}}>{uzLang?"REZYUME":"MY RESUME"}</h2>
                    <div className="container">
                 <div className="cvfon">
                     <Row>
                         <Col md={12} lg={12} sm={12} xs={12}>
                             <h2 data-aos={"zoom-in-right"} style={{color:"white",textAlign:"center",marginTop:"80px"}}>{uzLang?"CV | REZYUME":"CV | RESUME"}</h2>
                         </Col>
                         <Col md={12} lg={12} sm={12} xs={12}>
                             <img className="myfoto"  data-aos={"zoom-in"}  style={{width:"30%",marginTop:"50px",marginLeft:"40%"}} src={myfoto} alt="Xatolik"/>
                         </Col>
                     </Row>
               <div data-aos={"fade-up"} className="savol-javob1">
                   <Row>
                       <Col md={12} lg={12} sm={17} xs={7}>
                           <h4 className="savol">{uzLang?"Ism:":"Name:"}</h4>
                       </Col>
                       <Col md={12} lg={12} sm={17} xs={17}>
                           <h4 className="javob">{uzLang?"Behro'zbek":"Behruzbek"}</h4>
                       </Col>
                   </Row>
                   <Row><Col md={24} lg={24} sm={24} xs={24}><div className="chiziq"></div></Col></Row>
               </div>

                     <div data-aos={"fade-up"} className="savol-javob">
                         <Row>
                             <Col md={12} lg={12} sm={17} xs={7}>
                                 <h4 className="savol">{uzLang?"Familiya:":"Surname:"}</h4>
                             </Col>
                             <Col md={12} lg={12} sm={17} xs={7}>
                                 <h4 className="javob">Nazarov</h4>
                             </Col>
                         </Row>
                         <Row><Col md={24} lg={24} sm={24} xs={24}><div className="chiziq"></div></Col></Row>
                     </div>

                     <div data-aos={"fade-up"} className="savol-javob">
                         <Row>
                             <Col md={12} lg={12} sm={17} xs={7}>
                                 <h4 className="savol">{uzLang?"Kasbi:":"Occupation:"}</h4>
                             </Col>
                             <Col md={12} lg={12} sm={17} xs={7}>
                                 <h4 className="javob">{uzLang?"Talaba":"Student"}</h4>
                             </Col>
                         </Row>
                         <Row><Col md={24} lg={24} sm={24} xs={24}><div className="chiziq"></div></Col></Row>
                     </div>

                     <div data-aos={"fade-up"} className="savol-javob">
                         <Row>
                             <Col md={12} lg={12} sm={17} xs={7}>
                                 <h4 className="savol">{uzLang?"Ta'lim: ":"Education:"}</h4>
                             </Col>
                             <Col md={12} lg={12} sm={17} xs={7}>
                                 <h4 className="javob">{uzLang?"TATU":"TUIT"}</h4>
                             </Col>
                         </Row>
                         <Row><Col md={24} lg={24} sm={24} xs={24}><div className="chiziq"></div></Col></Row>
                     </div>

                     <div data-aos={"fade-up"} className="savol-javob">
                         <Row>
                             <Col md={12} lg={12} sm={12} xs={9}>
                                 <h4 className="savol">{uzLang?"Tug'ilgan ma'lumotlari:":"Data of birth:"}</h4>
                             </Col>
                             <Col md={12} lg={12} sm={12} xs={15}>
                                 <h4 className="javob dear">{uzLang?"9- iyun 2001 yil":"9-June 2001 year"}</h4>
                             </Col>
                         </Row>
                         <Row><Col md={24} lg={24} sm={24} xs={24}><div className="chiziq chiziq2"></div></Col></Row>
                     </div>

                     <div data-aos={"fade-up"} className="savol-javob">
                         <Row>
                             <Col md={12} lg={12} sm={17} xs={7}>
                                 <h4 className="savol">{uzLang?"Hozirda yashash manzili:":"Currently resides:"}</h4>
                             </Col>
                             <Col md={12} lg={12} sm={17} xs={17}>
                                 <h4 className="javob">{uzLang?<div>Toshkent shahar <br/> Yunusobod tumani</div>:"Yunusabad district of Tashkent city"}</h4>
                             </Col>
                         </Row>
                         <Row><Col md={24} lg={24} sm={24} xs={24}><div className="chiziq chiziq1"></div></Col></Row>
                     </div>

                     <div data-aos={"fade-up"} className="savol-javob">
                         <Row>
                             <Col md={12} lg={12} sm={17} xs={6}>
                                 <h4 className="savol">{uzLang?"Tillar:":"Laungage:"}</h4>
                             </Col>
                             <Col md={12} lg={12} sm={17} xs={18}>
                                 <h4 className="javob">{uzLang?"O'zbekcha , Inglizcha":"Uzbek , English"}</h4>
                             </Col>
                         </Row>
                         <Row><Col md={24} lg={24} sm={24} xs={24}><div className="chiziq chiziq2"></div></Col></Row>
                     </div>
                     <div data-aos={"zoom-in"} className="download"><a className="downloada" href={myresume} download="Behruzbek_resume">{uzLang?"Yuklab olish":"Download CV"}</a></div>

                 </div>
                    </div>
                </section>
            </div>
        );
    }
}
const mapStateToProps = (state) => {

    return {
        uzLang: state.changeLang.uzLang,
    };
};
export default connect(mapStateToProps,{UzLanguage,RuLanguage})(
    Cvrezyume
)