import React,{Component} from "react";
import Particles1 from "./particls/particles";
import Myskill from "./particls/myskill";
import Myproekt from "./myproekt/myproekt";
import Cvrezyume from "./cvrezyume/cvrezyume";
import Footer from "./footer/footer";
import ScrollTopView from "./scroltotop/scrooltotop";
import Loader from "./loader/loader";

const Loading =()=>
    <div className="loading">
        <div></div>
        <div></div>
    </div>

class App extends Component {
    state = {
        loading: true,
    };
    componentDidMount(){
        this.isLoading = setTimeout(()=>{this.setState({loading: false})}, 2300);
    }
    componentWillUnmount() {
        clearTimeout(this.isLoading);
    }

    timer = () => setTimeout(()=>{
        this.setState({loading: false})
    }, 1300);
    render() {
        const {loading} = this.state;
        return (
            loading ? (<Loader/>)
                :(<div>
                    <Particles1/>
                    <Myskill/>
                    <Myproekt/>
                    <Cvrezyume/>
                    <Footer/>
                    <ScrollTopView/>
                </div>)
        )
    }
}
export default App;