import {GetEnLanguage, GetLanguage, GetRuLanguage,SetLocalstorage} from "../../Utilitil";

const initialState = {
    uzLang: true,
};

export const langReducer = (state = initialState, action) => {
    switch (action.type) {
        case "Uzlang":

            SetLocalstorage("Behruz","UZ")
            return {uzLang: true}
        case "RuLang":

            SetLocalstorage("Behruz","RU")
            return {
                uzLang: false,
            }
        default:

        {!GetRuLanguage()?SetLocalstorage("Behruz","RU"):SetLocalstorage("Behruz","UZ")}
            return {
                uzLang:true,
            };
    }
};