import React, {Component} from "react";
import {Row,Col} from "antd"
import "../css/mySkill.css"
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Navigation, Pagination, Scrollbar, A11y,EffectCoverflow } from 'swiper';
import "swiper/swiper.scss"
import "swiper/components/navigation/navigation.scss"
import "swiper/components/pagination/pagination.scss"
import css from "../images/css.png"
import sass from "../images/sass.png";
import bootstrapt from "../images/bootstrapt.png";
import js from "../images/js.png";
import ajax from "../images/ajax.png";
import jquery from "../images/jquery.png";
import react from "../images/react.png";
import git from "../images/git.png";
import github from "../images/github.png";
import gitlab from "../images/gitlab.png"
import java from "../images/java.png";
import python from "../images/python.jpg";
import cplyus from "../images/cplyus.png";
import 'aos/dist/aos.css';
import AOS from 'aos'
import {connect} from "react-redux";
import {UzLanguage} from "../Redux/Actions/UzLanguage";
import {RuLanguage} from "../Redux/Actions/RuLanguage";
SwiperCore.use([Navigation,Pagination,Scrollbar,A11y,EffectCoverflow]);
AOS.init({
    duration: 1500,
})

class Myskill extends Component{
    render() {
        const {uzLang}=this.props;
        return (
            <div className=" section " id="myskills">
                <section >
             <div className="resume">
                <h2 data-aos={"zoom-in"}  className="myskill"><strong>{uzLang?"MAHORAT ":"MY SKILL"}</strong></h2>
                 {/*Front end slider*/}
             <div>
                 <Row className="container yozuv">
                     <Col  data-aos={"zoom-in"} md={10} lg={10} sm={12} xs={24} >
                         <p  className="yozuv-f">Front-end</p>
                         <div className="hr"></div>
                     </Col>
                     <Col md={14} lg={14} sm={12} xs={24}>
                         <div data-aos={"zoom-in"} style={{marginTop:"100px"}}>
                             <Swiper
                                 effect="coverflow"
                                 // spaceBetween={10}
                                 slidesPerView={4}
                                 navigation
                                 pagination={{
                                     clickable: true,
                                 }}
                                 scrollbar={{ draggable: true }}
                             >
                                 <SwiperSlide>
                                     <div className="frontlogo">
                                         <img className="image"  style={{width:"100%"}}  src={ajax} alt="Xato"/>
                                         <h1 style={{color:"white",textAlign:"center"}}><strong>Ajax</strong></h1>
                                         <div className="overlay"></div>
                                     </div>
                                 </SwiperSlide>
                                 <SwiperSlide>
                                     <div className="frontlogo">
                                         <img className="image" style={{width:"100%"}} src={'./html.png'} alt="Xato"/>
                                         <h1 style={{color:"white",textAlign:"center"}}><strong>Html</strong></h1>
                                         <div className="overlay"></div>
                                     </div>
                                 </SwiperSlide>
                                 <SwiperSlide>
                                     <div className="frontlogo">
                                         <img className="image" style={{width:"100%"}} src={css} alt="Xato"/>
                                         <h1 style={{color:"white",textAlign:"center"}}><strong>Css</strong></h1>
                                         <div className="overlay"></div>
                                     </div>
                                 </SwiperSlide>
                                 <SwiperSlide>
                                     <div className="frontlogo">
                                         <img className="image" style={{width:"100%"}} src={bootstrapt} alt="Xato"/>
                                         <h1 style={{color:"white",textAlign:"center"}}><strong>Bootstrapt</strong></h1>
                                         <div className="overlay"></div>
                                     </div>
                                 </SwiperSlide>
                                 <SwiperSlide>
                                     <div className="frontlogo">
                                         <img className="image" style={{width:"100%"}} src={sass} alt="Xato"/>
                                         <h1 style={{color:"white",textAlign:"center"}}><strong>Sass</strong></h1>
                                         <div className="overlay"></div>
                                     </div>
                                 </SwiperSlide>
                                 <SwiperSlide>
                                     <div className="frontlogo">
                                         <img className="image" style={{width:"100%"}} src={js} alt="Xato"/>
                                         <h1 style={{color:"white",textAlign:"center"}}><strong>JavaScript</strong></h1>
                                         <div className="overlay"></div>
                                     </div>
                                 </SwiperSlide>
                                 <SwiperSlide>
                                     <div className="frontlogo">
                                         <img className="image" style={{width:"100%"}} src={jquery} alt="Xato"/>
                                         <h1 style={{color:"white",textAlign:"center"}}><strong>Jquery</strong></h1>
                                         <div className="overlay"></div>
                                     </div>
                                 </SwiperSlide>
                                 <SwiperSlide>
                                     <div className="frontlogo">
                                         <img className="image" style={{width:"100%"}} src={react} alt="Xato"/>
                                         <h1 style={{color:"white",textAlign:"center"}}><strong>React</strong></h1>
                                         <div className="overlay"></div>
                                     </div>
                                 </SwiperSlide>
                                 <SwiperSlide>
                                     <div className="frontlogo">
                                         <img className="image" style={{width:"100%"}} src={git} alt="Xato"/>
                                         <h1 style={{color:"white",textAlign:"center"}}><strong>Git</strong></h1>
                                         <div className="overlay"></div>
                                     </div>
                                 </SwiperSlide>
                                 <SwiperSlide>
                                     <div className="frontlogo">
                                         <img className="image" style={{width:"100%"}} src={github} alt="Xato"/>
                                         <h1 style={{color:"white",textAlign:"center"}}><strong>GitHub</strong></h1>
                                         <div className="overlay"></div>
                                     </div>
                                 </SwiperSlide>
                                 <SwiperSlide>
                                     <div className="frontlogo">
                                         <img className="image" style={{width:"100%"}} src={gitlab} alt="Xato"/>
                                         <h1 style={{color:"white",textAlign:"center"}}><strong>GitLab</strong></h1>
                                         <div className="overlay"></div>
                                     </div>
                                 </SwiperSlide>
                             </Swiper>
                         </div>
                     </Col>
                 </Row>
             </div>
{/*front end slider end*/}

                 <Row className="container yozuv">
                     <Col data-aos={"zoom-in"}  md={10} lg={10} sm={12} xs={24}>
                         <p  className="yozuv-f">Backend-end</p>
                         <div className="hr"></div>
                     </Col>
                     <Col data-aos={"zoom-in"} md={14} lg={14} sm={12} xs={24}>
                         <div  style={{marginTop:"100px"}}>
                             <Swiper
                                 effect="coverflow"
                                 // spaceBetween={20}
                                 slidesPerView={3}
                                 navigation
                                 pagination={{
                                     clickable: true,
                                 }}
                                 scrollbar={{ draggable: true }}
                             >
                                 <SwiperSlide>
                                     <div className="frontlogo">
                                         <img className="image"  style={{width:"100%"}}  src={python} alt="Xato"/>
                                         <h1 style={{color:"white",textAlign:"center"}}><strong>Python</strong></h1>
                                         <div className="overlay"></div>
                                     </div>
                                 </SwiperSlide>
                                 <SwiperSlide>
                                     <div className="frontlogo">
                                         <img className="image" style={{width:"100%"}} src={java} alt="Xato"/>
                                         <h1 style={{color:"white",textAlign:"center"}}><strong>Java</strong></h1>
                                         <div className="overlay"></div>
                                     </div>
                                 </SwiperSlide>
                                 <SwiperSlide>
                                     <div className="frontlogo">
                                         <img className="image" style={{width:"100%"}} src={cplyus} alt="Xato"/>
                                         <h1 style={{color:"white",textAlign:"center"}}><strong>C++</strong></h1>
                                         <div className="overlay"></div>
                                     </div>
                                 </SwiperSlide>
                             </Swiper>
                         </div>
                     </Col>
                 </Row>

             </div>
                </section>

            </div>
        );
    }
}
const mapStateToProps = (state) => {

    return {
        uzLang: state.changeLang.uzLang,
    };
};
export default connect(mapStateToProps,{UzLanguage,RuLanguage})(
    Myskill
)